import {Component, DynamicComponentLoader, Directive, Input, Output, ViewContainerRef, ElementRef, EventEmitter, OnInit} from '@angular/core';
import {ColorpickerService} from './colorpicker.service';

@Directive({
  selector: '[colorpicker]',
  host: {
    '(click)': 'onClick()'
  }
})

export class Md2Colorpicker implements OnInit {
  @Input('colorpicker') colorpicker: string;
  @Input('position') cPosition: string = 'bottom';
  @Input('offset') offset: string = '0';
  @Input('format') format: string = 'hex';
  @Output('change') change = new EventEmitter<string>();
  private colorpickerDialog: any;
  private created: boolean;

  constructor(private dcl: DynamicComponentLoader, private vcRef: ViewContainerRef, private el: ElementRef, private service: ColorpickerService) {
    this.created = false;
  }

  ngOnInit() {
    let hsva = this.service.stringToHsva(this.colorpicker);
    if (hsva !== null) {
      this.change.emit(this.service.outputFormat(hsva, this.format));
    }
  }

  onClick() {
    if (!this.created) {
      this.created = true;
      this.dcl.loadNextToLocation(ColorpickerComponent, this.vcRef)
        .then((res) => {
          res.instance.setColorpickerDialog(this, this.el, this.colorpicker, this.cPosition, this.offset, this.format);
          this.colorpickerDialog = res.instance;
        });
    } else if (this.colorpickerDialog) {
      this.colorpickerDialog.setInitialColor(this.colorpicker);
      this.colorpickerDialog.openColorpicker();
    }
  }

  colorChanged(value: string) {
    this.change.emit(value)
  }
}

@Directive({
  selector: '[colorpicker-slider]',
  host: {
    '(mousedown)': 'start($event)',
    '(touchstart)': 'start($event)'
  }
})

export class ColorpickerSliderDirective {
  @Input('colorpicker-slider') slider: string;
  @Input('point-x') pointX: number;
  @Input('point-y') pointY: number;
  @Output('change') change = new EventEmitter<any>();
  private listenerMove: any;
  private listenerStop: any;

  constructor(private el: ElementRef) {
    this.listenerMove = (event: any) => { this.move(event) };
    this.listenerStop = () => { this.stop() };
  }

  setCursor(event: any) {
    let height = this.el.nativeElement.offsetHeight;
    let width = this.el.nativeElement.offsetWidth;
    let x = Math.max(0, Math.min(this.getX(event), width));
    let y = Math.max(0, Math.min(this.getY(event), height));

    if (this.pointX !== undefined && this.pointY !== undefined) {
      this.change.emit({ s: x / width, v: (1 - y / height), pointX: this.pointX, pointY: this.pointY });
    } else if (this.pointX === undefined && this.pointY !== undefined) {
      this.change.emit({ v: y / height, rg: this.pointY });
    } else {
      this.change.emit({ v: x / width, rg: this.pointX });
    }
  }

  move(event: any) {
    event.preventDefault();
    this.setCursor(event);
  }

  start(event: any) {
    this.setCursor(event);
    document.addEventListener('mousemove', this.listenerMove);
    document.addEventListener('touchmove', this.listenerMove);
    document.addEventListener('mouseup', this.listenerStop);
    document.addEventListener('touchend', this.listenerStop);
  }

  stop() {
    document.removeEventListener('mousemove', this.listenerMove);
    document.removeEventListener('touchmove', this.listenerMove);
    document.removeEventListener('mouseup', this.listenerStop);
    document.removeEventListener('touchend', this.listenerStop);
  }

  getX(event: any) {
    return (event.pageX !== undefined ? event.pageX : event.touches[0].pageX) - this.el.nativeElement.getBoundingClientRect().left - window.pageXOffset;
  }

  getY(event: any) {
    return (event.pageY !== undefined ? event.pageY : event.touches[0].pageY) - this.el.nativeElement.getBoundingClientRect().top - window.pageYOffset;
  }
}

@Component({
  selector: 'color-picker',
  template: `
    <div class="md2-color-picker" *ngIf="show" [style.height.px]="actualHeight" [style.top.px]="top" [style.left.px]="left" [style.position]="position">
      <div [colorpicker-slider] [style.background-color]="hueSliderColor" [point-x]="1" [point-y]="1" (change)="setSaturationAndBrightness($event)" class="saturation-lightness">
        <div [style.left.px]="slider.s" [style.top.px]="slider.v" class="cursor"></div>
      </div>
      <div [colorpicker-slider] [point-x]="1" (change)="setHue($event)" class="hue">
        <div [style.left.px]="slider.h" class="cursor"></div>
      </div>
      <div [style.background-color]="outputColor" class="selected-color"></div>
      <button type="button" class="md2-color-picker-cancel-btn" (click)="cancelColor()">Cancel</button>
    </div>
  `,
  styles: [`
    .md2-color-picker, .md2-color-picker * { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; margin: 0; font-size: 11px; }
    .md2-color-picker { width: 200px; height: 220px; left: 30px; top: 250px; position: absolute; z-index: 1000; background-color: #fff; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -moz-transition-duration: 5s; -o-transition-duration: 5s; -webkit-transition-duration: 5s; transition-duration: 5s; box-shadow: 0 7px 8px -4px rgba(0,0,0,.2),0 13px 19px 2px rgba(0,0,0,.14),0 5px 24px 4px rgba(0,0,0,.12); border-radius: 4px; }
    .md2-color-picker i { cursor: default; position: relative; }
    .md2-color-picker .md2-color-picker-cancel-btn { position: absolute; bottom: 12px; right: 3%; border-radius: 3px; padding: 2px 7px; -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; background: transparent; text-align: center; overflow: hidden; cursor: pointer; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; outline: none; border: none; display: inline-block; white-space: nowrap; text-decoration: none; vertical-align: middle; font-size: 12px; font-weight: 500; text-transform: uppercase; line-height: 26px; -webkit-transition: background 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); -moz-transition: background 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); -o-transition: background 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); transition: background 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26); color: rgba(0, 0, 0, 0.87059); background-color: #fafafa; }
    .md2-color-picker .md2-color-picker-cancel-btn:hover { background-color: rgba(158,158,158,0.2); }
    .md2-color-picker div.cursor-sv { cursor: default; position: relative; -moz-border-radius: 50%; -webkit-border-radius: 50%; border-radius: 50%; width: 15px; height: 15px; border: #ddd solid 1px; }
    .md2-color-picker div.cursor { cursor: crosshair; position: relative; -moz-border-radius: 50%; -webkit-border-radius: 50%; border-radius: 50%; width: 15px; height: 15px; box-shadow: 0 0 2px 0 rgba(0,0,0,.5),inset 0 0 2px 0 rgba(0,0,0,.5); border: 2px solid #fff; }
    .md2-color-picker .saturation-lightness { width: 100%; height: 130px; border: none; top: 0; left: 0; position: absolute; background-image: url("data:image/png;base64,"); overflow: hidden; border-radius: 4px 4px 0 0; }
    .md2-color-picker .saturation-lightness:hover { cursor: crosshair; }
    .md2-color-picker .hue { width: 150px; height: 16px; border: none; bottom: 65px; left: 25px; position: absolute; background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAJYAAAAQCAYAAAD06IYnAAAABmJLR0QA/wD/AP+gvaeTAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAB3RJTUUH4AIWDwkUFWbCCAAAAFxJREFUaN7t0kEKg0AQAME2x83/n2qu5qCgD1iDhCoYdpnbQC9bbY1qVO/jvc6k3ad91s7/7F1/csgPrujuQ17BDYSFsBAWwgJhISyEBcJCWAgLhIWwEBYIi2f7Ar/1TCgFH2X9AAAAAElFTkSuQmCC"); }
    .md2-color-picker .selected-color { width: 70px; height: 45px; bottom: 10px; left: 2%; position: absolute; border: 1px solid rgb(204, 204, 204); }
  `],
  directives: [ColorpickerSliderDirective]
})

export class ColorpickerComponent implements OnInit {
  private hsva: Hsva;
  private outputColor: string;
  private hueSliderColor: string;
  private slider: SliderPosition;
  private sliderDim: SliderDimension;
  private show: boolean;
  private top: number;
  private left: number;
  private position: string;
  private directiveInstance: any;
  private initialColor: string;
  private directiveElementRef: ElementRef;
  private mouseDown: any;
  private resize: any;
  private cPosition: string;
  private offset: number;
  private format: string;
  private actualHeight: number = 220;
  private actualWidth: number = 220;

  constructor(private el: ElementRef, private service: ColorpickerService) { }

  setColorpickerDialog(instance: any, elementRef: ElementRef, color: any, cPosition: string, offset: string, format: string) {
    this.directiveInstance = instance;
    this.initialColor = color;
    this.directiveElementRef = elementRef;
    this.cPosition = cPosition;
    this.offset = parseInt(offset);
    this.format = format;
  }

  setInitialColor(color: any) {
    this.initialColor = color;
  }

  ngOnInit() {
    let hsva = this.service.stringToHsva(this.initialColor);
    if (hsva !== null) {
      this.hsva = hsva;
    } else {
      this.hsva = new Hsva(0, 1, 1, 1);
    }
    this.sliderDim = new SliderDimension(150, 200, 130, 150);
    this.slider = new SliderPosition(0, 0, 0, 0);
    this.mouseDown = (event: any) => { this.onMouseDown(event) };
    this.resize = () => { this.onResize() };
    this.update();
    this.openColorpicker();
  }

  openColorpicker() {
    if (!this.show) {
      this.setColorpickerDialogPosition();
      this.show = true;
      document.addEventListener('mousedown', this.mouseDown);
      window.addEventListener('resize', this.resize);
    }
  }

  onMouseDown(event: any) {
    if (!this.isDescendant(this.el.nativeElement, event.target)
      && event.target != this.directiveElementRef.nativeElement) {
      this.closeColorpicker();
    }

  }

  closeColorpicker() {
    this.show = false;
    document.removeEventListener('mouseup', this.mouseDown);
    window.removeEventListener('resize', this.resize);
  }

  onResize() {
    if (this.position === 'fixed') {
      this.setColorpickerDialogPosition();
    }
  }

  setColorpickerDialogPosition() {
    var node = this.directiveElementRef.nativeElement, position = 'static';
    let parentNode = null;
    while (node !== null && node.tagName !== 'HTML') {
      position = window.getComputedStyle(node).getPropertyValue("position");
      if (position !== 'static' && parentNode === null) {
        parentNode = node;
      }
      if (position === 'fixed') {
        break;
      }
      node = node.parentNode;
    }
    if (position !== 'fixed') {
      var boxDirective = this.createBox(this.directiveElementRef.nativeElement, true);
      if (parentNode === null) { parentNode = node }
      let boxParent = this.createBox(parentNode, true);
      this.top = boxDirective.top - boxParent.top;
      this.left = boxDirective.left - boxParent.left;
    } else {
      var boxDirective = this.createBox(this.directiveElementRef.nativeElement, false);
      this.top = boxDirective.top;
      this.left = boxDirective.left;
      this.position = 'fixed';
    }
    if (this.cPosition === 'left') {
      this.top += boxDirective.height * this.offset / 100;
      this.left -= this.actualWidth;
    } else if (this.cPosition === 'top') {
      this.top -= this.actualHeight;
      this.left += this.offset / 100 * boxDirective.width;
    } else if (this.cPosition === 'right') {
      this.top += boxDirective.height * this.offset / 100;
      this.left += boxDirective.width;
    } else {
      this.top += boxDirective.height;
      this.left += this.offset / 100 * boxDirective.width;
    }
  }

  setHue(val: { v: number, rg: number }) {
    this.hsva.h = val.v / val.rg;
    this.update();
  }

  setSaturationAndBrightness(val: { s: number, v: number, pointX: number, pointY: number }) {
    this.hsva.s = val.s / val.pointX;
    this.hsva.v = val.v / val.pointY;
    this.update();
  }

  setColorFromString(value: string) {
    let hsva = this.service.stringToHsva(value);
    if (hsva !== null) {
      this.hsva = hsva;
    }
    this.update();
  }

  update() {
    let hsla = this.service.hsva2hsla(this.hsva);
    let rgba = this.service.denormalizeRGBA(this.service.hsvaToRgba(this.hsva));
    let hueRgba = this.service.denormalizeRGBA(this.service.hsvaToRgba(new Hsva(this.hsva.h, 1, 1, 1)));

    this.hueSliderColor = 'rgb(' + hueRgba.r + ',' + hueRgba.g + ',' + hueRgba.b + ')';

    this.outputColor = this.service.outputFormat(this.hsva, this.format);

    this.slider = new SliderPosition((this.hsva.h) * this.sliderDim.h - 8, this.hsva.s * this.sliderDim.s - 8,
      (1 - this.hsva.v) * this.sliderDim.v - 8, this.hsva.a * this.sliderDim.a - 8)

    this.directiveInstance.colorChanged(this.outputColor);
  }

  cancelColor() {
    this.setColorFromString(this.initialColor);
    this.closeColorpicker();
  }

  isDescendant(parent, child) {
    var node = child.parentNode;
    while (node !== null) {
      if (node === parent) {
        return true;
      }
      node = node.parentNode;
    }
    return false;
  }

  createBox(element, offset) {
    return {
      top: element.getBoundingClientRect().top + (offset ? window.pageYOffset : 0),
      left: element.getBoundingClientRect().left + (offset ? window.pageXOffset : 0),
      width: element.offsetWidth,
      height: element.offsetHeight
    };
  }
}

export class Hsva {
  constructor(public h: number, public s: number, public v: number, public a: number) { }
}
export class Hsla {
  constructor(public h: number, public s: number, public l: number, public a: number) { }
}
export class Rgba {
  constructor(public r: number, public g: number, public b: number, public a: number) { }
}
export class SliderPosition {
  constructor(public h: number, public s: number, public v: number, public a: number) { }
}
export class SliderDimension {
  constructor(public h: number, public s: number, public v: number, public a: number) { }
}
